'use client';

import { useEffect, useState } from 'react';
import { useRouter } from 'next/navigation';
import { useAuth } from '@/hooks/useAuth';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Avatar, AvatarFallback, AvatarImage } from '@/components/ui/avatar';
import { Skeleton } from '@/components/ui/skeleton';
import { usePageBackground } from '@/hooks/usePageBackground';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import * as z from 'zod';
import { Form, FormControl, FormDescription, FormField, FormItem, FormLabel, FormMessage } from '@/components/ui/form';
import { Input } from '@/components/ui/input';
import { Textarea } from '@/components/ui/textarea';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Edit2 } from 'lucide-react';
import { Calendar } from '@/components/ui/calendar';
import { useToast } from '@/hooks/use-toast';
import { Label } from '@/components/ui/label';

const profileSchema = z.object({
  studentName: z.string().min(2, { message: "Name must be at least 2 characters." }),
  dob: z.date({
    required_error: "A date of birth is required.",
  }),
  address: z.string().optional(),
  schoolName: z.string().optional(),
  grade: z.string({ required_error: "Please select a grade." }),
  mobileNo: z.string().optional(),
  whatsappNo: z.string().optional(),
  profilePhoto: z.any().optional(),
});

function calculateAge(dob: Date) {
  const diff_ms = Date.now() - dob.getTime();
  const age_dt = new Date(diff_ms);
  return Math.abs(age_dt.getUTCFullYear() - 1970);
}

const gradeOptions = [
    { value: '1', label: '1st' },
    { value: '2', label: '2nd' },
    { value: '3', label: '3rd' },
    { value: '4', label: '4th' },
    { value: '5', label: '5th' },
    { value: '6', label: '6th' },
    { value: '7', label: '7th' },
    { value: '8', label: '8th' },
    { value: '9', label: '9th' },
    { value: '10', label: '10th' },
];

export default function ProfilePage() {
  usePageBackground('https://placehold.co/1920x1080/f3e8ff/6b21a8?text=.');
  const router = useRouter();
  const { user, logout, isLoading } = useAuth();
  const { toast } = useToast();
  const [age, setAge] = useState<number | null>(null);

  const form = useForm<z.infer<typeof profileSchema>>({
    resolver: zodResolver(profileSchema),
    defaultValues: {
      studentName: '',
      address: '',
      schoolName: '',
      mobileNo: '',
      whatsappNo: '',
    },
  });

  useEffect(() => {
    if (!isLoading && !user) {
      router.push('/login');
    }
    if (user) {
      form.setValue('studentName', user.displayName || user.email?.split('@')[0] || '');
    }
  }, [user, isLoading, router, form]);

  const { watch } = form;
  const dob = watch('dob');

  useEffect(() => {
    if (dob) {
      setAge(calculateAge(dob));
    }
  }, [dob]);
  
  const onSubmit = (values: z.infer<typeof profileSchema>) => {
    console.log(values);
    toast({
      title: "Profile Updated",
      description: "Your information has been saved successfully.",
    });
  };

  if (isLoading || !user) {
    return (
       <div className="max-w-2xl mx-auto">
        <Card className="shadow-lg">
          <CardHeader className="flex flex-row items-center gap-4">
             <Skeleton className="h-24 w-24 rounded-full" />
             <div className="space-y-2">
                <Skeleton className="h-6 w-48" />
                <Skeleton className="h-4 w-32" />
             </div>
          </CardHeader>
          <CardContent className="space-y-4 mt-4">
             <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <Skeleton className="h-10 w-full" />
                <Skeleton className="h-10 w-full" />
             </div>
             <Skeleton className="h-20 w-full" />
             <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <Skeleton className="h-10 w-full" />
                <Skeleton className="h-10 w-full" />
             </div>
             <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <Skeleton className="h-10 w-full" />
                <Skeleton className="h-10 w-full" />
             </div>
             <Skeleton className="h-10 w-full" />
          </CardContent>
        </Card>
       </div>
    );
  }
  
  const displayName = user.displayName || user.email?.split('@')[0] || 'User';
  const displayInitial = displayName.charAt(0).toUpperCase();

  return (
    <div className="max-w-4xl mx-auto">
      <Card className="shadow-lg">
        <CardHeader>
            <CardTitle className="text-3xl font-headline">Student Profile</CardTitle>
            <CardDescription>View and edit your personal information.</CardDescription>
        </CardHeader>
        <CardContent>
            <Form {...form}>
                <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-8">
                    <div className="flex items-center gap-6">
                        <div className="relative">
                            <Avatar className="h-24 w-24 border-2 border-primary">
                                <AvatarImage src={user.photoURL || `https://placehold.co/100x100.png`} alt={displayName} />
                                <AvatarFallback>{displayInitial}</AvatarFallback>
                            </Avatar>
                            <FormField
                                control={form.control}
                                name="profilePhoto"
                                render={({ field }) => (
                                    <FormItem>
                                        <FormControl>
                                            <label htmlFor="photo-upload" className="absolute bottom-0 right-0 bg-primary text-primary-foreground p-2 rounded-full cursor-pointer hover:bg-primary/90 transition-colors">
                                                <Edit2 className="h-4 w-4" />
                                                <input id="photo-upload" type="file" className="sr-only" onChange={(e) => field.onChange(e.target.files)} />
                                            </label>
                                        </FormControl>
                                        <FormMessage />
                                    </FormItem>
                                )}
                            />
                        </div>
                        <div className="space-y-1">
                            <h2 className="text-2xl font-bold">{form.watch('studentName') || displayName}</h2>
                            <p className="text-muted-foreground">{user.email}</p>
                        </div>
                    </div>
                    
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                        <FormField
                            control={form.control}
                            name="studentName"
                            render={({ field }) => (
                                <FormItem>
                                    <FormLabel>Student Name</FormLabel>
                                    <FormControl><Input placeholder="Full Name" {...field} /></FormControl>
                                    <FormMessage />
                                </FormItem>
                            )}
                        />
                        <FormField
                            control={form.control}
                            name="dob"
                            render={({ field }) => (
                                <FormItem>
                                    <FormLabel>Date of Birth</FormLabel>
                                    <FormControl>
                                      <Calendar
                                          mode="single"
                                          captionLayout="dropdown-buttons"
                                          fromYear={1950}
                                          toYear={new Date().getFullYear()}
                                          selected={field.value}
                                          onSelect={field.onChange}
                                          disabled={(date) => date > new Date() || date < new Date("1900-01-01")}
                                          defaultMonth={field.value || new Date(new Date().setFullYear(new Date().getFullYear() - 10))}
                                          className="rounded-md border"
                                      />
                                    </FormControl>
                                    <FormMessage />
                                </FormItem>
                            )}
                        />
                    </div>
                    
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                         <div className="space-y-2">
                             <Label>Age</Label>
                             <Input value={age !== null ? `${age} years old` : 'Select DOB to calculate'} disabled />
                         </div>
                        <FormField
                            control={form.control}
                            name="schoolName"
                            render={({ field }) => (
                                <FormItem>
                                    <FormLabel>School Name</FormLabel>
                                    <FormControl><Input placeholder="School Name" {...field} /></FormControl>
                                    <FormMessage />
                                </FormItem>
                            )}
                        />
                    </div>

                    <FormField
                        control={form.control}
                        name="address"
                        render={({ field }) => (
                            <FormItem>
                                <FormLabel>Address</FormLabel>
                                <FormControl><Textarea placeholder="Your address" {...field} /></FormControl>
                                <FormMessage />
                            </FormItem>
                        )}
                    />

                    <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                        <FormField
                            control={form.control}
                            name="grade"
                            render={({ field }) => (
                                <FormItem>
                                <FormLabel>Grade/Std.</FormLabel>
                                <Select onValueChange={field.onChange} defaultValue={field.value}>
                                    <FormControl>
                                    <SelectTrigger>
                                        <SelectValue placeholder="Select a grade" />
                                    </SelectTrigger>
                                    </FormControl>
                                    <SelectContent>
                                    {gradeOptions.map((option) => (
                                        <SelectItem key={option.value} value={option.value}>
                                            {option.label}
                                        </SelectItem>
                                    ))}
                                    </SelectContent>
                                </Select>
                                <FormMessage />
                                </FormItem>
                            )}
                        />
                        <FormField
                            control={form.control}
                            name="mobileNo"
                            render={({ field }) => (
                                <FormItem>
                                    <FormLabel>Mobile No.</FormLabel>
                                    <FormControl><Input placeholder="Mobile Number" {...field} /></FormControl>
                                    <FormMessage />
                                </FormItem>
                            )}
                        />
                    </div>
                     <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                        <FormField
                            control={form.control}
                            name="whatsappNo"
                            render={({ field }) => (
                                <FormItem>
                                    <FormLabel>WhatsApp No.</FormLabel>
                                    <FormControl><Input placeholder="WhatsApp Number" {...field} /></FormControl>
                                    <FormMessage />
                                </FormItem>
                            )}
                        />
                     </div>

                    <div className="flex justify-end gap-4 pt-4">
                        <Button type="button" variant="destructive" onClick={() => logout().then(() => router.push('/'))}>Log Out</Button>
                        <Button type="submit">Save Changes</Button>
                    </div>
                </form>
            </Form>
        </CardContent>
      </Card>
    </div>
  );
}
